import Ajax from './ajax';

const Request = {
  User: {
    info() {
      return Ajax.get('/account/info');
    }
  },
  Dict: {
    /**
     * 
     * @param code
     * dic_code
     * @returns {*}
     */
     get(code) {
      return Ajax.get('/manager/common/dic',{code:code});
    }
  },
  Home: {
    getMessageList() {
      return {
        'status': 200,
        'body': [{
          'id': 1,
          'isReaded': false,
          'title': '1',
          'description': ''
        }, {
          'id': 2,
          'isReaded': false,
          'title': '2',
          'description': ''
        }, {
          'id': 3,
          'isReaded': true,
          'title': '3',
          'description': ''
        }, {
          'id': 4,
          'isReaded': true,
          'title': '4',
          'description': ''
        }, {
          'id': 5,
          'isReaded': true,
          'title': '5',
          'description': ''
        }]
      };
    }
  },
  Login: {
    login(param) {
      return Ajax.postJson('/login', param);
    },
    logout(param) {
      return Ajax.post('/logout', param);
    }
  },
  TestData:{
    getData(){
      return Ajax.get('http://localhost:5008/qryCrawlerList')
    }
  }
};

export default Request;
